@import (less) "color.less";
:host{
  display:inline-block;
  width:inherit;
}
.nbpAlertContainer {
    width: 550px;
    //  width: 100%;
    //  max-width: 550px;
    min-height: 72px;
    display: table;
    table-layout: fixed;
    -webkit-box-shadow: 0px 0px 17px 0px rgba(0, 0, 0, 0.18);
    -moz-box-shadow: 0px 0px 17px 0px rgba(0, 0, 0, 0.18);
    box-shadow: 0px 0px 17px 0px rgba(0, 0, 0, 0.18);
    .nbpAlertIconContainer {
        display: table-cell;
        vertical-align: middle;
        text-align: center;
        padding: 14px 18px;
        font-size: 36px;
        width: 72px;
        line-height:1;
        .icon:before{
          vertical-align: middle;
        }
    }
    .nbpAlertMessageContainer {
        display: table-cell;
        vertical-align: middle;
        text-align: justify;
        position: relative;
        font-size: 16px;
        color: @darkest_gray;
        padding: 14px 50px 14px 18px;
        .nbpAlertClosingIcon {
            position:absolute;
            top:14px;
            right:18px;
            font-size: 20px;
            cursor: pointer;
            .icon:before{
              vertical-align: initial;
            }
        }
        div {
          vertical-align:middle;
        }
    }
    &.informativa {
        border: 2px solid @alertbox_bk_green;
        .nbpAlertIconContainer {
            color: @lightest_green;
            background-color: @alertbox_bk_green;
        }
    }
    &.informativa-positiva {
        border: 2px solid @alertbox_bk_green;
        .nbpAlertIconContainer {
            color: @lightest_green;
            background-color: @alertbox_bk_green;
        }
    }
    &.informativa-promozionale {
        border: 2px solid @alertbox_bk_blue;
        .nbpAlertIconContainer {
            color: @alertbox_Icon_cyan;
            background-color: @alertbox_bk_blue;
        }
    }
    &.warning {
        border: 2px solid @alertbox_bk_orange;
        .nbpAlertIconContainer {
            color: @alertbox_Icon_orange;
            background-color: @alertbox_bk_orange;
        }
    }
    &.warning-bloccante {
        border: 2px solid @alertbox_bk_red;
        .nbpAlertIconContainer {
            color: @alertbox_Icon_red;
            background-color: @alertbox_bk_red;
        }
    }
}

:host-context(.contrasto-on) {
  .nbpAlertContainer {
    border: 2px solid @black;
        .nbpAlertIconContainer {
          font-weight:bold;
          color: @white;
          background-color: @black;
        }
        .nbpAlertMessageContainer {
          color:@black;
        }
    }
}